import React, { Component } from 'react'
import Header from '../../components/Header'
import Topic from '../../components/TopicItem/topicItem'
import Aside from '../../components/Aside'
import { message } from 'antd'
import { topicList } from '../../servers/index'
import './index.css'

export default class ArticleList extends Component {
    constructor() {
        super()
        this.state = {
            list: [],
            page: 1,
            has_next: false,
            isDataArrive: true
        }
    }
    componentDidMount() {
        const page = this.state.page
        const page_size = 10
        const page_num = page
        this.getArticleList(page_num, page_size)

        window.addEventListener('scroll', this.loadMore.bind(this));
    }
    componentWillUnmount() {
        window.removeEventListener('scroll', this.loadMore.bind(this))
    }

    loadMore() {
        let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
        let clientHeight = document.documentElement.clientHeight || document.body.clientHeight;
        let scrollHeight = document.documentElement.scrollHeight || document.body.scrollHeight;
        if (scrollTop + clientHeight === scrollHeight) {
            console.log("到底部了")
            this.getMoreArticleList()
        }
    }

    getArticleList(page_num, page_size) {
        const username = this.props.match.params.username
        if (username) {
            const data = {
                page_num,
                page_size
            }
            topicList(data, username).then(res => {
                if (res.code === 200) {
                    const { has_next, next_number } = res.data.pageInfo
                    this.setState({ list: res.data.topics, has_next, page: next_number })
                } else {
                    message.error(res.error)
                }
            })
        }

    }

    //加载更多
    getMoreArticleList() {
        const { isDataArrive, has_next, page } = this.state
        if (!isDataArrive || !has_next || page === 1) {
            return
        }
        this.setState({ isDataArrive: false })
        const data = {
            page_num: page,
            page_size: 10
        }
        const username = this.props.match.params.username
        topicList(data, username).then(res => {
            if (res.code === 200) {
                const { has_next, next_number } = res.data.pageInfo
                this.setState({
                    list: [...this.state.list, ...res.data.topics],
                    has_next, page: next_number, isDataArrive: true
                })
            } else {
                message.error(res.error)
            }
        })

    }

    render() {
        const { list } = this.state
        return (
            <div>
                <Header {...this.props} />
                <img src={require('../../assets/imgs/page_bg1.png')} className="figure-img img-fluid w-100" alt="" />
                <div className="container">
                    <div className="public-header" style={{ marginBottom: 20 }}>
                        <span className="header-name">文章列表</span>
                        <span>不要轻易放弃。学习成长的路上，我们长路漫漫，只因学无止境。 </span>
                    </div>
                    <div className="row">
                        <div className="col-md-8">
                            {list.map((item, index) => {
                                return (
                                    <Topic data={item} key={index} {...this.props} />
                                )
                            })}

                            {list.length === 0 ?
                                <div className="no-data">暂无数据</div> : null
                            }

                        </div>
                        <div className="col-md-4">
                            <Aside />
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}